import React from 'react';
import { Form, Input, Modal } from 'antd';

const AddWebsiteForm = ({ open, onCancel, onSubmit }) => {
  const [form] = Form.useForm();

  const handleOk = async () => {
    try {
      const values = await form.validateFields();
      await onSubmit(values);
      form.resetFields();
    } catch (error) {
      console.error(':', error);
    }
  };

  return (
    <Modal
      title=""
      open={open}
      onCancel={onCancel}
      onOk={handleOk}
      destroyOnClose
    >
      <Form form={form} layout="vertical">
        <Form.Item
          name="title"
          label=""
          rules={[{ required: true, message: '' }]}
        >
          <Input placeholder="" />
        </Form.Item>

        <Form.Item
          name="url"
          label=""
          rules={[
            { required: true, message: '' },
            { type: 'url', message: 'URL' }
          ]}
        >
          <Input placeholder="" />
        </Form.Item>

        <Form.Item
          name="description"
          label=""
        >
          <Input.TextArea placeholder="" />
        </Form.Item>

        <Form.Item
          name="icon"
          label=""
        >
          <Input placeholder="" />
        </Form.Item>
      </Form>
    </Modal>
  );
};

export default AddWebsiteForm; 