//  Personal Access Token  OAuth
export const AuthService = {
  //  token
  setToken(token) {
    localStorage.setItem('github_token', token);
  },

  //  token
  getToken() {
    return localStorage.getItem('github_token');
  },

  // 
  isAuthenticated() {
    return !!this.getToken();
  },

  // 
  logout() {
    localStorage.removeItem('github_token');
  }
};
